.App {
  background-color: antiquewhite;
  width: 31vw;
  height: 90vh;
  margin-left: 28rem;
  text-align: center;
}
.list-items{
  height: 70vh;
}
.btn{
  padding: 10px 70px 10px 70px;
  border-radius: 8px;
  color: white;
  background-color: rgb(88, 80, 69);
  border: 1px solid whitesmoke;
  cursor: pointer;
  font-weight: bold;
}
.btn:hover{
  background-color: rgb(53, 36, 36);
}
article{
  display: flex;
  height: 80px;
  gap: 20px;
  align-items: center;
}
.height{
  height: 44px;
  border-radius: 10px;
  padding-left: 24px;
}
.main-div{
  display: flex;
  align-items: center;
  gap: 20px;
}